@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

.navbar-brand {
   font-weight: bold !important;
   font-family: "Grand Hotel", cursive;
   font-size: 1.7rem;
}

.navbar {
   background-color: #fff !important;
   border-bottom: 1px solid lightgray;
}

.nav__icon__btn {
   border: 0px;
   outline: none !important;
   color: black !important;
}

.story {
   border: 1px solid lightgray;
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 0.5rem;
   overflow-x: scroll;
   background-color: #ffff;
}

.avatar__border {
   border: 2px solid;
   border-color: rgba(214, 80, 206, 1);
}

.post {
   margin-top: 20px;
   margin-bottom: 20px;
   border: 1px solid lightgray;
   background-color: #ffff;
}

.post__header {
   display: flex;
   align-items: center;
   justify-content: space-between;
   border-bottom: 1px solid lightgray;
}

.post__header__username {
   display: flex;
   align-items: center;
}

.post__header__icon__btn {
   outline: none !important;
}

.post__img img {
   max-height: 450px;
   width: 100%;
}

.post__action {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.post__action__lsm__icon__btn {
   outline: none !important;
}

.post__like__counter h6 {
   padding: 0px 15px 10px 15px;
}

.post__caption {
   padding-left: 15px;
}
.post__comment {
   padding: 0px 15px 5px 15px;
}

.post__comment__btn {
   color: rgb(25, 70, 112) !important;
   font-weight: bolder !important;
   outline: none !important;
   font-size: 1.3rem !important;
}

.post__comments {
   padding-left: 15px;
   padding-right: 15px;
}

.post__comments p {
   font-size: 0.8rem;
   word-wrap: break-word;
}

.main__body {
   margin-top: 100px;
}
.main__body__suggestion {
   position: fixed !important;
   top: 101px;
   right: 2vw;
   background-color: #f5f5f5 !important;
}

.suggestion__profile {
   display: flex;
   align-items: center;
   padding: 15px;
}

.suggestion__profile__username {
   margin-left: 10px;
   line-height: 2px;
}

.suggestion__profiles__seeall {
   color: black;
   font-weight: bold;
   font-size: small;
}

.suggestion__profiles__seeall:hover {
   cursor: pointer;
}

.suggestion__profile__details {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.suggestion__policies a {
   font-size: 0.8rem;
   color: rgb(184, 184, 184) !important;
   text-decoration: none;
}

.suggestion__policies small {
   font-size: 0.8rem;
   color: rgb(184, 184, 184) !important;
}

.suggestion__policies {
   width: 350px;
   padding: 0px 10px 0px 30px;
}

